.header__section {
    height: 230px;

    @include for-tablet-up {
        height: 216px;
    }
}

.header__wrapper {
    background: url(./../images/abstract-background-with-red-lines-mobile.jpg);
    opacity: 0.9;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.header__section {
    background-color: var(--bg-black-color);
}

.header__container {

    @include for-tablet-up {
        display: flex;
        flex-direction: column;
        text-align: center;
    }
}

.site-nav {
    display: flex;
    justify-content: space-between;

    margin-bottom: 54px;

    @include for-tablet-up {
        margin-bottom: 40px;
    }
}

.logo__link {
    stroke: var(--white-color);
    color: var(--white-color);

    &:hover,
    &:focus {
        stroke: var(--active-element);
        color: var(--active-element);
    }

    @include for-tablet-up {
        display: flex;
        align-items: center;
    }
}

// .logo__icon {}
.logo__name {
    display: none;
    font-size: 30px;
    line-height: 1.16;
    margin-left: 8px;


    @include for-tablet-up {
        display: block;
    }
}

.nav__list {
    display: flex;
    align-items: center;
}

.nav__item {
    font-size: 12px;
    line-height: 14px;

    text-transform: uppercase;
    color: var(--white-color);

    &:not(:last-child) {
        margin-right: 40px;
    }
}

.nav__link {

    &:hover,
    &:focus {
        border-bottom: 3px solid var(--active-element);
    }
}

.nav__link--active {
    border-bottom: 3px solid var(--active-element);
}

.search__form-wrapper {
    margin: 0 auto;
}

.search__form {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-bottom: 4px;

    border-bottom: 0.5px solid var(--white-color);

    &:hover,
    &:focus-within {
        border-bottom: 0.5px solid var(--active-element);
    }

    @include for-tablet-up {
        width: 336px;
        margin: 0 auto;
    }

    @include for-desktop-up {
        width: 394px;
    }
}

.search__input {
    background-color: transparent;
    border: 0;
    padding: 0;
    color: inherit;

    width: 100%;
}

.search__btn {
    display: flex;
    align-items: center;

    width: 16px;
    height: 16px;

    background-color: transparent;
    stroke: var(--white-color);

    cursor: pointer;

    &:hover,
    &:focus {
        stroke: var(--active-element);
    }
}

.search__error-input {
    display: none;
    font-size: 14px;
    margin: 16px 0 0;
    color: var(--active-element);

    @include for-tablet-up {
        margin-top: 20px;
    }
}